import dayjs from 'dayjs';
import { SortingType } from '../const';
import { returnDateDuration } from './point';

const sorting = {
  [SortingType.DAY]: (points) => points.sort((a, b) => dayjs(a.dateFrom) > dayjs(b.dateFrom) ? -1 : 1),
  [SortingType.EVENT]: () => {},
  [SortingType.TIME]: (points) => points.sort((a, b) => returnDateDuration(a.dateFrom, a.dateTo) > returnDateDuration(b.dateFrom, b.dateTo) ? -1 : 1),
  [SortingType.PRICE]: (points) => points.sort((a, b) => a.basePrice > b.basePrice ? -1 : 1),
  [SortingType.OFFERS]: () => {}
};

export {sorting};
